<template>
    <div style="margin-top: 5%; margin-left: 20%;">
         <!-- 
        data   
        props  
               label   	
               children	
               disabled	
               isLeaf	   lazy   ()
        @node-click 
    -->
      <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick" 
        style="color: white; font-size: larger;"
      ></el-tree>
    </div>
  </template>
  
  <script>
    import axios from 'axios';
import { Tree } from 'element-ui';
    export default {
      components: {
      [Tree.name]: Tree
    },
    name: "tree1",
    data() {
      return {
        data: [
          {
            label: " 2",
            children: [
              {
                label: " 2-1",
                children: [
                  {
                    label: " 2-1-1",
                    id:1,
                  },
                ],
              },
              {
                label: " 2-2",
                children: [
                  {
                    label: " 2-2-1",
                    id:2,
                  },
                ],
              },
            ],
          },
        ],
        defaultProps: {
          children: "children",
          label: "label",
        },
      };
    },
    methods: {
      sendEvent(id) {
      this.$emit('childEvent', id);
    },
      handleNodeClick(data) {
        if(data.children==undefined){
          this.sendEvent(data.id)
        }
      },
      getArticleTree(){
        const ipAddress = '127.0.0.1';
        const port = '4000';
        //  URL
        const apiUrl = `http://${ipAddress}:${port}/getArticleTree`;
        axios.get(apiUrl)
        .then(Response=>{
            this.data=Response.data;
        })
        .catch(error=>{
          console.log('error while fetching articleTree!',error);
        })
      },
    },
    mounted(){
      this.getArticleTree();
    }
  };
  </script>
  
  
